      this.categorias = data.categorias;
     // this.Metodo_filtrar_producto();
      this.Metodo_buscar_producto_interno();
    },
    methods: {
      Metodo_link_inicio(){     
        if(this.link_actual === "inicio"){
         // location.reload();
        } else {
          this.$router.push('/');
        }
      }, 
      Metodo_buscar_producto_interno(){
        if(this.palabra_buscar === ''){
        // Inicio listar todos los productos
                axios.get(config.url_api + 'productos_stock/')
                .then(res => {
                    this.lista_productos_encontrados = res.data;
                });
        // Fin de listar todos los productos
        } else {
        //Inicio de listar productos buscados
        return axios.get(`${config.url_api}productos_stock/buscar/?query=${this.palabra_buscar}`)
          .then(res => {
              this.lista_productos_encontrados = res.data;
          });
        //Fin de listar productos buscados                  
        }
        },
      Metodo_buscar_producto_interno(){
