        // Fin de listar todos los productos
        } else {
        //Inicio de listar productos buscados
        return axios.get(`${config.url_api}productos_stock/buscar/?query=${this.palabra_buscar}`)
          .then(res => {
              this.lista_productos_encontrados = res.data;
          });
        //Fin de listar productos buscados                  
        }
