                this.detalle_producto = res.data;   
               // console.log(this.detalle_producto.subsubcategoria); 
                this.Metodo_productos_relacionados(this.detalle_producto.subsubcategoria);               
            });
// Fin de metodo obtener detalle producto
        },
        Metodo_obtener_detalle_producto_interno(id_producto){
// Inicio de obtener detalle producto interno
            this.$router.replace({
                path: id_producto
            });
            axios.get(config.url_api + 'productos_stock/detalle_producto/' + id_producto)
            .then(res => {
                this.detalle_producto = res.data;   
               // console.log(this.detalle_producto.subsubcategoria); 
                this.Metodo_productos_relacionados(this.detalle_producto.subsubcategoria);               
            });
            window.scrollTo(0, 0);
// Fin de obtener detalle producto interno
        }, 
        Metodo_listar_comentarios_interno(id_producto){
// Inicio de metodo listar galerias imagenes 
            axios.get(config.url_api + 'comentarios/comentarios_x_producto/' + id_producto)
            .then(res =>{
                this.lista_comentarios = res.data;
            });
// Fin de metodo listar galerias imagenes 
        }, 

        Metodo_listar_galeria_imagenes(){
// Inicio de metodo listar galerias imagenes 
            axios.get(config.url_api + 'galeria_imagenes/' + this.id_producto)
            .then(res =>{
                this.galeria_imagenes_productos = res.data;
            });
// Fin de metodo listar galerias imagenes 
        },
        Metodo_productos_relacionados(nombre_subsubcategoria){
// Inicio de metodo productos relacionados            
        return axios.get(`${config.url_api}productos_stock/productos_relacionados/?query=${nombre_subsubcategoria}`)
          .then(res => {
              this.lista_productos_relacionados = res.data;
              //console.log(this.lista_productos_relacionados);
              //this.lista_productos_encontrados = res.data;
          });
// Fin de metodo productos relacionados              
        },
        Metodo_detalle_producto(id_producto){
            axios.get(config.url_api + 'productos_stock/detalle_producto/' + id_producto)
            .then(res => {
                this.detalle_producto = res.data;                  
            });
        },
        
        Metodo_comprar_producto(){

// Inicio de metodo comprar producto            
        if(!this.detalle_producto.nombre_producto || 
          !this.detalle_compra.cantidad_compra ||
          !this.detalle_compra.nombre_cliente ||
          !this.detalle_compra.direccion_cliente ||
          !this.detalle_compra.correo_cliente ||
          !this.detalle_compra.celular_cliente
        ){
            this.error_compra = "datos incompletos";
            this.alerta_registrar_compra = true;          
          }else{
                if(this.detalle_compra.celular_cliente.length === 9){            
                    if(!this.reg.test(this.detalle_compra.correo_cliente)){
                        this.error_compra = "correo inválido";
                        this.alerta_registrar_compra = true;
                    } else {            
                  const data_registrar_compra = {
                  nombre_producto: this.detalle_producto.nombre_producto,
                  id_producto: this.detalle_producto._id,
                  cantidad_compra: this.detalle_compra.cantidad_compra,
                  nombre_cliente: this.detalle_compra.nombre_cliente,
                  direccion_cliente: this.detalle_compra.direccion_cliente,
                  correo_cliente: this.detalle_compra.correo_cliente,
                  celular_cliente: this.detalle_compra.celular_cliente
                  } 
                    axios.post(config.url_api + 'compras/', data_registrar_compra)
                    .then(res => {
                      if(res.data.success === true){
                          this.dialogo_comprar_producto = false;
                          this.dialogo_compra_realizada = true; 
                          this.error_compra = "";
                          this.alerta_registrar_compra = false;                          
                          //Limpiar formulario
                          this.detalle_producto.nombre_producto = ""; 
                          this.detalle_producto._id = ""; 
                          this.detalle_compra.cantidad_compra = ""; 
                          this.detalle_compra.nombre_cliente = ""; 
                          this.detalle_compra.direccion_cliente = ""; 
                          this.detalle_compra.correo_cliente = ""; 
                          this.detalle_compra.celular_cliente = "";                                                  
                      } else {
                          this.error_compra = "no hay sistema";
                          this.alerta_registrar_compra = true; 
                          //Limpiar formulario
                          this.detalle_producto.nombre_producto = ""; 
                          this.detalle_producto._id = "";
                          this.detalle_compra.cantidad_compra = ""; 
                          this.detalle_compra.nombre_cliente = ""; 
                          this.detalle_compra.direccion_cliente = ""; 
                          this.detalle_compra.correo_cliente = ""; 
                          this.detalle_compra.celular_cliente = "";                           
                      }
                    });
                    }                           
                } else {
                        this.error_compra = "celular incorrecto";
                        this.alerta_registrar_compra = true;
                }
          }
// Fin de metodo comprar producto 

        },
        Metodo_listar_comentarios(){
// Inicio de metodo listar galerias imagenes 
            axios.get(config.url_api + 'comentarios/comentarios_x_producto/' + this.id_producto)
            .then(res =>{
                this.lista_comentarios = res.data;
            });
// Fin de metodo listar galerias imagenes 
        },        
        Metodo_publicar_comentario(){

// Inicio de registrar comentario            
           if(!this.detalle_comentario.nombre_cliente || 
            !this.detalle_comentario.correo_cliente ||
            !this.detalle_comentario.comentario_cliente 
            ){
                this.error_comentario = "datos incompletos";
                this.alerta_registrar_comentario = true;
            } else {
                if(!this.reg.test(this.detalle_comentario.correo_cliente)){
                    this.error_comentario = "correo inválido";
                    this.alerta_registrar_comentario = true;
                } else {
                        const data_registrar_comentario = {
                        id_producto: this.detalle_producto._id,
                        nombre_cliente: this.detalle_comentario.nombre_cliente,                        
                        correo_cliente: this.detalle_comentario.correo_cliente,
                        comentario_cliente: this.detalle_comentario.comentario_cliente
                        }
                        axios.post(config.url_api + 'comentarios/registrar_comentario', data_registrar_comentario)
                        .then(res => {
                        if(res.data.success === true){
                            this.dialogo_comentar = false; 
                            this.error_comentario = "";
                            this.alerta_registrar_comentario = false;  
                            this.snackbar_info = true;
                            this.text_snackbar = res.data.msg;        
                            //Limpiar formulario
                            this.detalle_comentario.nombre_cliente = ""; 
                            this.detalle_comentario.correo_cliente = ""; 
                            this.detalle_comentario.comentario_cliente = ""; 
                            this.Metodo_listar_comentarios_interno(this.detalle_producto._id);
                                                                          
                        } else {
                            this.alerta_registrar_comentario = false;
                            this.snackbar_error = true;
                            this.text_snackbar = "Error con el servidor";
                            //Limpiar formulario
                            this.detalle_comentario.nombre_cliente = ""; 
                            this.detalle_comentario.correo_cliente = ""; 
                            this.detalle_comentario.comentario_cliente = ""; 
                            this.Metodo_listar_comentarios_interno(this.detalle_producto._id);                       
                        }
                        })                        
                }
                
            }
// Fin de registrar comentario 

            }
      
    } 
}
</script>