import Vue from 'vue'
import VueRouter from 'vue-router'


import Inicio from '../views/Inicio.vue'
import Detalle_producto from '../views/Detalle_producto.vue'
import Tienda from '../views/Tienda.vue'
import Busqueda from '../views/Busqueda.vue'
import Filtrar from '../views/Filtro.vue'
import Catalogo from '../views/Catalogo.vue'
import Regalos from '../views/Regalos.vue'
import Contacto from '../views/Contacto.vue'
import Nosotros from '../views/Nosotros.vue'
import Canastas from '../views/Canastas.vue'
import Escrow from '../views/Escrow.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'inicio',
    component: Inicio
  },
  {
    path: '/detalle_producto/:id',
    name: 'detalle_producto',
    component: Detalle_producto
  },
  {
    path: '/tienda',
    name: 'tienda',
    component: Tienda,
  },
  {
    path: '/productos',
    name: 'productos',
    component: Busqueda,
    props: route => ({ query: route.query.buscar }),
  },
  {
    path: '/productos_filtrar',
    name: 'productos_filtrar',
    component: Filtrar,
    props: route => ({ query: route.query.filtro }),
  },
  {
    path: '/catalogo',
    name: 'catalogo',
    component: Catalogo,
    props: route => ({ query: route.query.producto }),    
  },
  {
    path: '/regalos',
    name: 'regalos',
    component: Regalos
  },
  {
    path: '/contacto',
    name: 'contacto',
    component: Contacto
  },
  {
    path: '/nosotros',
    name: 'nosotros',
    component: Nosotros
  },
  {
    path: '/canastas',
    name: 'canastas',
    component: Canastas
  },
  {
    path: '/escrow',
    name: 'escrow',
    component: Escrow
  }  


]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  linkActiveClass: "active",
  linkExactActiveClass: "active"
})

export default router
