                                                        <div slot="label">Número de celular</div>
                                                    </v-text-field>
                                                </v-flex>
                                                <v-flex xs12 class="pl-3 pr-3">
                                                    <v-text-field outlined dense type="text" required v-model="detalle_compra.correo_cliente" >
                                                        <div slot="label">Correo electrónico</div>
                                                    </v-text-field>
                                                </v-flex>
                                            </v-layout>                         
                                        </v-container>
                                        <v-divider class="mx-5"></v-divider>
                                        <v-card-actions>
                                            <v-spacer></v-spacer>
                                                <v-btn
                                                color="#1DC460"
                                                dark
                                                depressed type="submit"
                                                >  Siguiente <i class="fas fa-arrow-right ml-1"></i>
                                                
                                                </v-btn>
                                        </v-card-actions>                        
                                    </form>
                                </v-card>
                            </v-dialog>
                <!-- Fin comprar producto -->

                <!-- Inicio de compra realizada con exito -->
                            <v-dialog v-model="dialogo_compra_realizada" width="350px">
                                <v-card>
                                        <v-card-title class="info py-4 title" style="color: white;">
                                            Compra realizada
                                            <v-spacer> </v-spacer>
                                            <v-btn icon dark @click="dialogo_compra_realizada = false">
                                                <i class="fas fa-times fa-lg"></i>
                                            </v-btn>
                                        </v-card-title>
                                        <v-container grid-list-sm class="pt-5 pb-5">
                                            <div class="pa-4 text-center">
                                            <v-img
                                                class="mb-4"
                                                contain
                                                height="150"
                                                src="../assets/like.png"
                                            ></v-img>
                                            <h3 class="title font-weight-light mb-2">Gracias por su comprar, en breve nos comunciaremos contigo para la entrega.</h3>
                                            <span class="caption grey--text">Que tenga un buen día!</span>
                                            </div>
                                        </v-container> 
                                </v-card>               
                            </v-dialog>
                <!-- Fin de compra realizada con exito -->

                <!-- Inicio de filtro de productos -->
                    <v-dialog v-model="dialogo_filtrar_producto" width="380px">                  
                            <v-card
                                max-width="500"
                                class="mx-auto"
                            >
                                <v-toolbar
                                color="#FF9900"
                                dark
                                >
                                <v-toolbar-title>Filtrar búsqueda</v-toolbar-title>
                                <v-spacer></v-spacer>
                                <v-btn icon dark @click="dialogo_filtrar_producto = false">
                                    <i class="fas fa-times fa-lg"></i>
                                </v-btn>
                                </v-toolbar>
                                <v-list>  
                                <v-list-group  
                                    color="#FF9900"                     
                                    v-for="categoria in categorias"
                                    :key="categoria.title"
                                >
                                    <template v-slot:activator>
                                    <v-list-item-title v-text="categoria.title"></v-list-item-title>
                                    </template>
                                    <v-list-group
                                    color="#FF9900" 
                                    no-action
                                    sub-group
                                    v-for="subcategoria in categoria.subcategorias"
                                    :key="subcategoria.title"
                                    >
                                    <template v-slot:activator>
                                        <v-list-item-content>
                                        <v-list-item-title v-text="subcategoria.title"></v-list-item-title>
                                        </v-list-item-content>
                                    </template>
                                    <v-list-item
                                        link
                                        v-for="subsubcategoria in subcategoria.subsubcategorias"
                                        :key="subsubcategoria.title"
                                        @click="Metodo_filtrar_producto(subsubcategoria.nombrebd), dialogo_filtrar_producto = false"
                                    >
                                        <v-list-item-title v-text="subsubcategoria.title"></v-list-item-title>
                                    </v-list-item>
                                    </v-list-group>
                                </v-list-group>                                                                        
                                </v-list>                         
                            </v-card>                               
                    </v-dialog>
                <!-- Fin de filtro de productos -->    
                
                <!-- Inicio de boton de llamar y whatsapp -->
                    <v-btn light x-large class="font-weight-bold title hidden-sm-and-up" color="info" fixed left bottom width="145px" onclick="window.open('tel:+51999909380')">
                            <i style="color: white" class="fas fa-phone-volume fa-lg pr-1 pb-1"></i> Llamar
                    </v-btn>
                    <v-btn dark x-large class="font-weight-bold title hidden-sm-and-up" color="#00BB2D" fixed right bottom width="175px" onclick="window.open('https://bit.ly/394gsKP','_blank')">
                        <i style="color: white" class="fab fa-whatsapp fa-lg pr-1 pb-1"></i> Whatsapp 
                    </v-btn>
                <!-- Fin de boton de llamar y whatsapp -->

                <!-- Inicio de botones redes sociales -->
                        <template>                            
                            <v-layout column class="fab-container hidden-sm-and-down">                                
                                <div class="text-center mb-3 ml-6">
                                    <v-btn large class="mx-1 mb-5" fab dark color="#0099e5" onclick="window.open('https://m.me/tukuyshopperu','_blank')">
                                        <i style="color: white" class="fab fa-facebook-messenger fa-3x"></i>
                                    </v-btn>
                                    <v-btn large class="mx-1 mb-5" fab dark color="#0abf53" onclick="window.open('https://bit.ly/394gsKP','_blank')">
                                        <i style="color: white" class="fab fa-whatsapp fa-3x"></i>
                                    </v-btn>
                                    <v-btn large class="mx-1 mb-5" fab dark color="#0D47A1" onclick="window.open('https://www.facebook.com/tukuyshopperu','_blank')">
                                        <i style="color: white" class="fab fa-facebook fa-3x"></i>
                                    </v-btn>
                                </div> 
                            </v-layout> 
                        </template> 
                <!-- Fin de  botones redes sociales-->                             

            </v-container>

        <!-- Inicio de pie de pagina -->
                <div>
                    <v-card
                    id="contenedor-pie-pagina"
                    flat
                    tile
                    color="#FF9900"
                    class="white--text text-center"
                    >                                       
                    <v-card-title>
                        <v-row>
                            <v-flex grow xs12 md8 sm8>
                                <span class="subtitle-1">Copyright © {{ new Date().getFullYear() }} Todos los derechos reservados, Tukuy Shop S.A.C</span>
                            </v-flex>
                            <v-flex grow xs12 md4 sm8>
                                <v-btn class="mx-1" icon dark color="#0099e5" onclick="window.open('https://m.me/tukuyshopperu','_blank')">
                                    <i style="color: white" class="fab fa-facebook-messenger fa-2x"></i>
                                </v-btn>
                                <v-btn class="mx-1" icon dark color="#0099e5" onclick="window.open('https://bit.ly/394gsKP','_blank')">
                                    <i style="color: white" class="fab fa-whatsapp fa-2x"></i>
                                </v-btn>
                                <v-btn class="mx-1" icon dark color="#0099e5" onclick="window.open('https://www.facebook.com/tukuyshopperu','_blank')">
                                    <i style="color: white" class="fab fa-facebook fa-2x"></i>
                                </v-btn>

