                        </v-row>                                            
                    </v-card-title>
                    
                    </v-card>

                </div>
        <!-- Fin de Pie de pagina -->            
        </v-main>
    </v-app>
</template>
<script>
import axios from 'axios'
const config = require("../config/config");
const data = require("../config/data");

export default {
    name: 'Catalogo',
    
    data() {
        return {
           producto: this.$route.query.producto,
           categorias: [],
           numero_base: "1",
           lista_productos_catalogo: [],
           dialogo_comprar_producto: false,
           dialogo_compra_realizada: false,
           alerta_registrar_compra: false,
           error_compra: [],
           detalle_producto: [],
           detalle_compra: [],
           palabra_buscar: "",
           dialogo_filtrar_producto: false,
           reg: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/,  


        };
    },
    created(){
        
    },
    mounted(){
       this.Metodo_filtrar_producto_interno();
       this.categorias = data.categorias;
    },
    methods: {
        Metodo_filtrar_producto_interno(){          
            //Inicio de filtrar productos        
            return axios.get(`${config.url_api}productos_stock/filtrar/?query=${this.producto}`)
            .then(res => {
                this.lista_productos_catalogo = res.data;
            });
            //Fin de filtrar productos    
        },
        Metodo_detalle_producto(id_producto){
            //Inicio de Metodo obtener detalle producto
            axios.get(config.url_api + 'productos_stock/detalle_producto/' + id_producto)
            .then(res => {
                this.detalle_producto = res.data;                  
            });
            //Fin de Metodo obtener detalle producto
        }, 
        Metodo_comprar_producto(){
            //Inicio de metodo registrar compra
            if(!this.detalle_producto.nombre_producto || 
            !this.detalle_compra.cantidad_compra ||
            !this.detalle_compra.nombre_cliente ||
            !this.detalle_compra.direccion_cliente ||
            !this.detalle_compra.correo_cliente ||
            !this.detalle_compra.celular_cliente
            ){
                this.error_compra = "datos incompletos";
                this.alerta_registrar_compra = true;          
            }else{
                if(this.detalle_compra.celular_cliente.length === 9){            
                    
                    if(!this.reg.test(this.detalle_compra.correo_cliente)){
                        this.error_compra = "correo inválido";
                        this.alerta_registrar_compra = true;
                    } else { 
                        const data_registrar_compra = {
                        nombre_producto: this.detalle_producto.nombre_producto,
                        id_producto: this.detalle_producto._id,
                        cantidad_compra: this.detalle_compra.cantidad_compra,
                        nombre_cliente: this.detalle_compra.nombre_cliente,
                        direccion_cliente: this.detalle_compra.direccion_cliente,
                        correo_cliente: this.detalle_compra.correo_cliente,
                        celular_cliente: this.detalle_compra.celular_cliente
                        } 
                            axios.post(config.url_api + 'compras/', data_registrar_compra)
                            .then(res => {
                            if(res.data.success === true){
                                this.dialogo_comprar_producto = false;
                                this.dialogo_compra_realizada = true; 
                                this.error_compra = "";
                                this.alerta_registrar_compra = false;                          
                                //Limpiar formulario
                                this.detalle_producto.nombre_producto = ""; 
                                this.detalle_producto._id = ""; 
                                this.detalle_compra.cantidad_compra = ""; 
                                this.detalle_compra.nombre_cliente = ""; 
                                this.detalle_compra.direccion_cliente = ""; 
                                this.detalle_compra.correo_cliente = ""; 
                                this.detalle_compra.celular_cliente = "";                                                  
                            } else {
                                this.error_compra = "no hay sistema";
                                this.alerta_registrar_compra = true; 
                                //Limpiar formulario
                                this.detalle_producto.nombre_producto = ""; 
                                this.detalle_producto._id = "";
                                this.detalle_compra.cantidad_compra = ""; 
                                this.detalle_compra.nombre_cliente = ""; 
                                this.detalle_compra.direccion_cliente = ""; 
                                this.detalle_compra.correo_cliente = ""; 
                                this.detalle_compra.celular_cliente = "";                           
                            }
                            });                        
                    }                                                        
                } else {
                        this.error_compra = "celular incorrecto";
                        this.alerta_registrar_compra = true;
                }
            }
            //Fin de metodo registrar compra
        },
        Metodo_buscar_producto(){
            //Inicio de metodo buscar producto            
            this.$router.replace({
                // some route here
                path: 'productos', query: { buscar: this.palabra_buscar }
            }).catch(err => {});
            //Fin de metodo buscar producto 
        },
        Metodo_filtrar_producto(palabra_filtrar){
            //Inicio de filtrar productos      
            this.$router.replace({
                // some route here
                path: 'productos_filtrar', query: { filtro: palabra_filtrar }
            }).catch(err => {});
            //Fin de filtrar productos    
        },                             
    } 
}
</script>
<style>
#titulo_producto_catalogo{
        text-decoration: none; 
        color: white;
}
</style>