<template>
    <v-app>
        <navigation></navigation>
        <v-main id="fondo-body">
            <v-container>
                
            
            <!-- Inicio de canasta grafica -->
                <div justify="center" align="center" v-if="productos_canasta.length">
                    <v-card max-width="720px" id="fondo-canasta" max-height="720">
                        <v-avatar class="profile mt-5 mr-2 ml-2 mb-5" size="90" tile v-for="producto of productos_canasta" :key="producto._id" >
                            <v-img :src="producto.ruta_img_producto">
                            </v-img>
                        </v-avatar> 

                    </v-card>
                </div>
            <!-- Fin de canasta grafica -->
            <!--Inicio Canasta de productos -->
                <div align="center" >            
                    <v-card class="mt-5" max-width="720px">
                        <div class="pt-1 pb-1">
                            <div class="display-1 font-weight-bold mt-3">Canasta Navideña </div>
                        </div>
                        <v-card-text>
                            <div v-if="!productos_canasta.length"  align="center" class="subtitle-1" style="color: #0282F9"> 
                                Tu canasta navideña esta vacia, que esperas para llenarlo de productos.
                                 <v-img
                                    height="180"
                                    width="240"
                                    src="../assets/canasta.png"                                 
                                 > </v-img>
                                </div>
                            <div v-show="mostrar_mensaje" style="color: green;" > Ya hemos registrado su pedido, pero si desea pude armar otra canasta</div>
                            <v-simple-table v-if="productos_canasta.length" class="mt-3" fixed-header height="300px" >
                                <thead>
                                    <tr>
                                    <th class="text-left title">Img.</th>
                                    <th class="text-left title">Producto</th>
                                    <th class="text-left title">Quitar</th>
                                    <th class="text-center title">Cantidad</th>                                       
                                    </tr>                                                                                                                              
                                </thead>
                                <tbody>
                                    <tr v-for="(producto, index) of productos_canasta" :key="producto._id">
                                        <td>                                     
                                            <v-avatar class="profile " size="60" tile>
                                                <v-img :src="producto.ruta_img_producto">
                                                </v-img>
                                            </v-avatar> 
                                        </td>                                        
                                        <td class="subtitle-1">
                                            <router-link id="titulo_producto" v-bind:to="'/detalle_producto/' + producto.id_producto" target="_blank">
                                            {{producto.nombre_producto}}
                                            </router-link>
                                        </td>
                                        <td class="subtitle-1">
                                            <v-btn  dark small  color="error"
                                            @click="Metodo_quitar_producto(index, producto.subtotal)"
                                            >                             
                                            <i class=" fas fa-trash fa-lg"></i>
                                            </v-btn>
                                        </td>                                         
                                        <td class="subtitle-1 text-center">{{producto.cantidad_total}}</td>  
                                                                                                            
                                    </tr>
                                </tbody>
                            </v-simple-table>
                            <div>
                                <v-btn v-if="productos_canasta.length" class="ml-2 mt-2" dark color="#1DC460"
                                    @click="dialogo_comprar_canasta = true">                             
                                    Cotizar Canasta <i class="ml-1 fas fa-shopping-basket fa-lg"></i>
                                </v-btn>
                            </div>
      
                        </v-card-text>
                    </v-card>
                </div>
            <!-- Fin de Canasta de productos -->

            <!-- Inicio de lista de productos -->
                <div align="center" > 
                    <v-card class="mt-5" max-width="720px">
            <!-- Inicio de Buscador de productos -->
                            <v-text-field 
                                v-model="palabra_buscar"                  
                                flat
                                single-line
                                color="info"
                                outlined
                                
                                clearable
                                hide-details
                                append-icon="fas fa-search fa-sm"
                                label="Buscar producto"
                                v-on:keyup.enter="Metodo_buscar_producto"
                                @click:append="Metodo_buscar_producto"
                                @click:clear="Metodo_listar_productos"
                            />                                
            <!-- Fin de Buscador de productos -->
                    </v-card>                                          
                    <v-card class="mt-5" max-width="720px">            
                        <div>
                            <div class="display-1 font-weight-bold mt-3 pt-3 pb-2"> Lista de productos </div>
                        </div>
                        <v-simple-table class="pt-2 pl-3" fixed-header height="580px" >
                            <thead>  
                                <tr>
                                <th class="text-left title">Img.</th>
                                <th class="text-left title">Producto</th>
                                <th class="text-center title">Agregar</th>                               
                                
                                </tr>                                                                                                                                                             
                            </thead>
                            <tbody v-if="!lista_productos_encontrados.length">
                                    <tr v-for="producto in lista_productos" :key="producto._id">
                                        <td>                                     
                                            <v-avatar class="profile " size="60px" tile>
                                                <v-img :src="producto.ruta_img_producto">
                                                </v-img>
                                            </v-avatar> 
                                        </td>                                        
                                        <td class="subtitle-1">
                                            <router-link id="titulo_producto" v-bind:to="'/detalle_producto/' + producto._id" target="_blank">
                                            {{producto.nombre_producto}}
                                            </router-link>
                                        </td> 
                                        <td class="text-center">                                        
                                            <v-btn class="ml-2 mt-2"  dark small  color="info"
                                            @click="Metodo_info_producto(producto._id), dialogo_agregar_producto = true">                             
                                             <i class="fas fas fa-shopping-basket  fa-lg"></i>
                                            </v-btn>                                                                    
                                        </td>                                                                                 
                                                                                                                                                 
                                    </tr>
                            </tbody>
                        </v-simple-table>
                    </v-card>
                </div>
            <!-- Fin de lista de productos -->

            <!-- Inicio de dialogo agregar producto al carrito -->
                <v-dialog v-model="dialogo_agregar_producto" width="350px">
                    <v-card>
                        <form @submit.prevent="Metodo_agregar_producto">                            
                        <v-card-title class="py-4 title" style="color: white; background: #329EF4">
                        Agregar a la canasta
                        <v-spacer> </v-spacer>
                            <v-btn icon dark @click="dialogo_agregar_producto = false">
                                <i class="fas fa-times fa-lg"></i>
                            </v-btn>
                        </v-card-title>                        
                        <v-container grid-list-sm class="pa-4 text-center" >
                            <v-layout row wrap>
                                <v-flex md12 xs12 class="mt-3" >
                                    <div class="title text-center"> {{detalle_producto.nombre_producto}}</div>
                                    <v-flex xs12 >
                                        <img :src="detalle_producto.ruta_img_producto" height="200" />
                                    </v-flex>  
                                </v-flex> 
                                <v-flex md12 xs12 class="mt-3 mr-10 ml-10" >
                                    <v-text-field outlined dense type="number" v-model="carrito.cantidad_total" color="info"                                    
                                    label="Cantidad" required>
                                    </v-text-field>
                                </v-flex>                                                                                         
                            </v-layout>
                            <v-alert
                                v-model="alerta_error_agregar_producto"
                                type="error"
                                class="text-xs-center text-sm-center text-md-center text-lg-center"
                            > ¡Error, {{erros}}! <v-btn @click="alerta_error_agregar_producto = false" icon class="ml-3"> <i class="fas fa-times  fa-lg"></i> </v-btn>
                            </v-alert>
                        </v-container>
                        <v-card-actions>
                            <v-spacer> </v-spacer>
                            <v-btn class="subtitle-1 text-capitalize" color="error" @click="dialogo_agregar_producto = false">
                                <i class="fas fa-times mr-2 fa-sm"></i>
                                Cancelar
                            </v-btn>
                            <v-btn class="subtitle-1 text-capitalize" color="info" type="submit">
                            <i class="fas fa-plus mr-2 fa-sm"></i>
                                Agregar
                            </v-btn>
                         </v-card-actions>
                        </form>
                    </v-card>
                </v-dialog>
            <!-- Fin de de dialogo agregar producto al carrito-->

            <!-- Inicio dialogo comprar producto -->
                <v-dialog v-model="dialogo_comprar_canasta" width="360px">
                    <v-card>
                        <form @submit.prevent="Metodo_comprar_canasta" >
                            <v-card-title class="info py-4 title" style="color: white;">
                            Comprar canasta
                            <v-spacer> </v-spacer>
                            <v-btn icon dark @click="dialogo_comprar_canasta = false">
                                <i class="fas fa-times fa-lg"></i>
                            </v-btn>
                            </v-card-title>
                            <div>
                               <!-- <div class="pt-2 pl-3 pr-3 subtitle-1 text-center"> Gratis una tina mediana de plastico y envolvida de canasta!</div> -->
                            </div>
                            <div class="pl-3 pr-3" >                                                
                                    <v-alert
                                        v-model="alerta_registrar_compra"
                                        dense
                                        type="error"
                                        class="text-xs-center text-sm-center text-md-center text-lg-center"
                                    > ¡Error, {{error_compra}}! <v-btn @click="alerta_registrar_compra = false" icon class="ml-3"> <i class="fas fa-times  fa-lg"></i> </v-btn>
                                    </v-alert>                                                                                                                          
                                <v-row >
                                    <v-col cols="12" xs="12" md="12" class="">
                                        <div class="pl-1 pb-1" style="color: #1DC460;">Formulario de registro:</div>
                                        <v-text-field outlined dense type="number"  v-model="detalle_cliente.cantidad_canastas" required color="#1DC460">
                                            <div slot="label">Cantidad de Canastas  </div>
                                        </v-text-field>                                        
                                        <v-text-field outlined dense type="text"  v-model="detalle_cliente.nombre_completo" required color="#1DC460">
                                            <div slot="label">Nombre completo  </div>
                                        </v-text-field>
                                        <v-text-field outlined dense type="number" counter="9" v-model="detalle_cliente.celular" required color="#1DC460">
                                            <div slot="label">Número de Celular</div>
                                        </v-text-field> 
                                        <v-text-field outlined dense type="text"  v-model="detalle_cliente.direccion" required color="#1DC460">
                                            <div slot="label">Dirección de envio</div>
                                        </v-text-field> 
                                    </v-col> 
                                </v-row>                         
                            </div>
                            <v-divider class="mx-5"></v-divider>
                            <v-card-actions class="justify-center">
                                    <v-btn                                                                        
                                    color="#1DC460"
                                    dark
                                    depressed type="submit"
                                    >  COTIZAR CANASTA                                          
                                    </v-btn>
                            </v-card-actions>                        
                        </form>
                    </v-card>
                </v-dialog>
            <!-- Fin dialogo comprar producto -->

            <!-- Inicio de compra realizada con exito -->
                        <v-dialog v-model="dialogo_compra_exitosa" width="350px">
                            <v-card>
                                    <v-card-title class="info py-4 title" style="color: white;">
                                        Compra exitosa
                                        <v-spacer> </v-spacer>
                                        <v-btn icon dark @click="dialogo_compra_exitosa = false">
                                            <i class="fas fa-times fa-lg"></i>
                                        </v-btn>
                                    </v-card-title>
                                    <v-container grid-list-sm class="pt-5 pb-5">
                                        <div class="pa-4 text-center">
                                        <v-img
                                            class="mb-4"
                                            contain
                                            height="150"
                                            src="../assets/like.png"
                                        ></v-img>
                                        <h3 class="title font-weight-light mb-2">Gracias por su compra , en breve nos comunicaremos con usted.</h3>
                                        <span class="caption grey--text">Que tenga un excelente día!</span>
                                        </div>
                                    </v-container> 
                            </v-card>               
                        </v-dialog>
            <!-- Fin de compra realizada con exito -->  
            </v-container>

            <!-- Inicio de snackbars -->
                <v-snackbar v-model="snackbar_success" :timeout="timeout" color="#54AB53">
                    {{text_snackbar}}
                    <template v-slot:action="{ attrs }">
                    <v-btn color="white" text @click="snackbar_success = false" v-bind="attrs">
                        Cerrar
                    </v-btn> 
                    </template>   
                </v-snackbar> 
                <v-snackbar v-model="snackbar_error" :timeout="timeout" color="error">
                    {{text_snackbar}}
                    <template v-slot:action="{ attrs }">
                    <v-btn color="white" text @click="snackbar_error = false" v-bind="attrs">
                        Cerrar
                    </v-btn> 
                    </template>   
                </v-snackbar>
                <v-snackbar v-model="snackbar_info" :timeout="timeout" color="info">
                    {{text_snackbar}}
                    <template v-slot:action="{ attrs }">
                    <v-btn color="white" text @click="snackbar_info = false" v-bind="attrs">
                        Cerrar
                    </v-btn> 
                    </template>   
                </v-snackbar>
            <!-- Fin de snackbars -->


        </v-main>
    </v-app>
</template>
<script>
import axios from 'axios'
const configuraciones = require("../config/config");
export default {
    data(){
        return {
            lista_productos: [],
            palabra_buscar: "",
            lista_productos_encontrados: [],
            dialogo_agregar_producto: false,
            detalle_producto: [],
            carrito: [],
            alerta_error_agregar_producto: false,
            erros: [],
            text_snackbar: "",
            snackbar_success: false,
            snackbar_error: false,
            snackbar_info: false,
            timeout: 3000,
            lista_productos_carrito: [],
            total_pagar_carrito: [],
            historial_compras: [],
            //Datos canastas
            productos_canasta: [],
            total_presupuesto_canasta: 0,
            dialogo_comprar_canasta: false,
            detalle_cliente: [],
            alerta_registrar_compra: false,
            error_compra: [],
            dialogo_compra_exitosa: false,
            mostrar_mensaje: false,


        }
    },
    created(){
        let CanastasDB = JSON.parse(localStorage.getItem('productos_canasta'));
        let Total_pagar_canasta = localStorage.getItem('total_pagar_canasta');
        if(CanastasDB === null){
            this.productos_canasta = [];
            this.total_presupuesto_canasta = 0;
        }else {
            this.productos_canasta = CanastasDB;
            this.total_presupuesto_canasta = Total_pagar_canasta;
        }
    },
    mounted(){
        this.Metodo_listar_productos();
        window.scrollTo(0, 0);
            },
    methods: {

        Metodo_listar_productos(){
//Inicio listar productos
        axios.get(configuraciones.url_api + 'canastas/productos/')
        .then(res => {
            this.lista_productos = res.data;
        });
//Fin listar productos
        },
       Metodo_buscar_producto(){
//Inicio de Metodo buscar cliente
        axios.get(`${configuraciones.url_api}canastas/buscar_producto/?query=${this.palabra_buscar}`)
           .then(res => {
               this.lista_productos = res.data;
           });
//Fin de Metodo buscar cliente
        },
        Metodo_info_producto(id){
//Inicio de metodo info producto
        axios.get(configuraciones.url_api + 'canastas/detalle_producto/' + id)
            .then(res => {
                this.detalle_producto = res.data;
            });
//Fin de metodo info producto
        },
        Metodo_agregar_producto(){
            if(!this.carrito.cantidad_total){
                this.snackbar_error = true;
                this.text_snackbar = "Cantidad minima 1"
            } else {
                let subtotal_producto_canasta = this.detalle_producto.preciominorista * this.carrito.cantidad_total;
                let presupuesto_canasta = parseFloat(subtotal_producto_canasta) + parseFloat(this.total_presupuesto_canasta);

                const data_producto_canasta = {
                    cantidad_total: this.carrito.cantidad_total,
                    id_producto: this.detalle_producto._id,
                    nombre_producto: this.detalle_producto.nombre_producto,
                    ruta_img_producto: this.detalle_producto.ruta_img_producto,
                    precio_unidad: this.detalle_producto.preciominorista,
                    subtotal: subtotal_producto_canasta.toFixed(2),                
                }
                const ids_productos = this.productos_canasta.map(el => el.id_producto);
                let verificar_duplicado = ids_productos.includes(this.detalle_producto._id);
                if(verificar_duplicado === false){
                    this.productos_canasta.push(data_producto_canasta);
                    localStorage.setItem('productos_canasta', JSON.stringify(this.productos_canasta));
                    this.total_presupuesto_canasta = presupuesto_canasta.toFixed(2);
                    localStorage.setItem('total_pagar_canasta', this.total_presupuesto_canasta);
                    this.dialogo_agregar_producto = false;
                    this.carrito.cantidad_total = "";
                    this.snackbar_info = true;
                    this.text_snackbar = "Producto agregado a la canasta"
                    window.scrollTo(0, 0);
                } else {
                    this.snackbar_error = true;
                    this.text_snackbar = "Producto ya esta en su canasta"
                }
            }
        },
        Metodo_quitar_producto(index, subtotal){
            
            this.productos_canasta.splice(index, 1);
            localStorage.setItem('productos_canasta', JSON.stringify(this.productos_canasta));
            let presupuesto_canasta = parseFloat(this.total_presupuesto_canasta) - parseFloat(subtotal);
            this.total_presupuesto_canasta = presupuesto_canasta.toFixed(2);
            localStorage.setItem('total_pagar_canasta', this.total_presupuesto_canasta); 
            this.snackbar_info = true;
            this.text_snackbar = "Producto quitado correctamente"
        },  
        Metodo_comprar_canasta(){
            if(!this.detalle_cliente.nombre_completo ||
                !this.detalle_cliente.celular ||
                !this.detalle_cliente.direccion ||
                !this.detalle_cliente.cantidad_canastas
                ){
                    this.alerta_registrar_compra = true;
                    this.error_compra = "datos incompletos";
                } else {
                    const data_compra_canasta = {
                        nombre_completo: this.detalle_cliente.nombre_completo,
                        celular: this.detalle_cliente.celular,
                        direccion: this.detalle_cliente.direccion,
                        productos_canasta: this.productos_canasta,
                        precio_canasta: this.total_presupuesto_canasta,
                        cantidad_canastas: this.detalle_cliente.cantidad_canastas
                    }
                axios.post(configuraciones.url_api + 'canastas/registrar/', data_compra_canasta )
                    .then(res => {
                        this.alerta_registrar_compra = false,
                        this.alerta_registrar_compra = false;
                        this.error_compra = "";                    
                        this.dialogo_compra_exitosa = true;
                        this.dialogo_comprar_canasta = false;
                        this.productos_canasta = [];
                        this.total_presupuesto_canasta = 0;
                        localStorage.clear();  
                        this.mostrar_mensaje = true; 

                    });  
                }
        }
    }
    
}
</script>
<style>
#fondo-canasta{
    background: url('../assets/fondo-canasta.jpg');
    background-position: center;
	background-attachment: contain;
	background-size: cover;
}
#fondo-body{
    background: url('../assets/background-navidad.jpg');
    background-position: center;
	background-attachment: contain;
	background-size: cover;
}
</style>