<script>
import axios from 'axios'
const config = require("../config/config");


export default {
    name: 'Regalos',    
    data() {
        return {            
           lista_productos_encontrados: [],
           categorias: [],
           categoria_seleccionada: "",
           pagina: 1,
           productos_x_pagina: "",
           total_paginas: "",
           detalle_producto: "",
           regalos: [],
         //Dialogo validar sugerencias de regalo
            dialogo_validar_regalo: false,
            detalle_validacion: [],
            alerta_registrar_validacion: false,
            error_validacion: "",
        //Snackbard
            snackbar_error: false,
            snackbar_info: false,
            snackbar_actualizado: false,
            timeout: 3000,
            text_snackbar: [],
        //Dialogo registro completo
            dialogo_registro_completo: false,

        };
    },
    created(){
        this.Metodo_listar_productos();
        this.Metodo_listar_categorias(); 
        let RegalosDB = JSON.parse(localStorage.getItem('regalos-item'));
        if(RegalosDB === null){
            this.regalos = [];
        }else {
            this.regalos = RegalosDB;
        }
    },
    mounted(){
       
    },
    methods: {
      async Metodo_listar_productos(){
        // Inicio de Metodo de listar productos
             const res = await axios.get(config.url_api + 'productos_foraneos/regalos/')                            
              this.lista_productos_encontrados = res.data.docs; 
              this.pagina = res.data.page;
              this.total_paginas = res.data.totalPages;
              this.productos_x_pagina = res.data.limit;                                  
        // Fin de Metodo de listar productos
        },
       async Metodo_filtrar_producto(palabra_filtrar){
       //Inicio de metodo listar regalos filtrados     
              const res = await axios.get(`${config.url_api}productos_foraneos/filtrar_regalo/?categoria=${palabra_filtrar}`)              
              this.lista_productos_encontrados = res.data.docs;
              this.pagina = res.data.page;
              this.total_paginas = res.data.totalPages;                                       
        //Fin de metodo listar regalos filtrados
        },
        Metodo_listar_categorias(){
        // Inicio de Metodo Listar subsubcategorias                    
                    axios.get(config.url_api + 'ajustes_foraneos/categorias')
                    .then(res => {
                        this.categorias = res.data;              
                    });
        // Fin de Metodo Listar subsubcategorias            
        },  
        async Metodo_listar_sumar_productos(){
            if(!this.categoria_seleccionada){
                //Inicio de pagina siguiente por defecto
                let pagina_sumada = (this.pagina + 1);
                this.pagina = pagina_sumada;           
                const res = await axios.get(`${config.url_api}productos_foraneos/regalos/?page=${pagina_sumada}`) 
                this.lista_productos_encontrados = res.data.docs;   
                //Fin de pagina siguiente por defecto              
            } else {
                //Inicio de pagina siguiente con filtro   
                let pagina_sumada = (this.pagina + 1);
                this.pagina = pagina_sumada;                                   
                const res = await axios.get(`${config.url_api}productos_foraneos/filtrar_regalo/?categoria=${this.categoria_seleccionada}&page=${pagina_sumada}`)              
                this.lista_productos_encontrados = res.data.docs;
                this.pagina = res.data.page;
                this.total_paginas = res.data.totalPages;  
                //Fin de pagina siguiente con filtro
            }                      
        },
        async Metodo_restar_producto(){
            if(!this.categoria_seleccionada){
                //Inicio de pagina siguiente por defecto
                let pagina_sumada = (this.pagina - 1);
                this.pagina = pagina_sumada;           
                const res = await axios.get(`${config.url_api}productos_foraneos/regalos/?page=${pagina_sumada}`) 
                this.lista_productos_encontrados = res.data.docs;   
                //Fin de pagina siguiente por defecto              
            } else {
                //Inicio de pagina siguiente con filtro   
                let pagina_sumada = (this.pagina - 1);
                this.pagina = pagina_sumada;                                   
                const res = await axios.get(`${config.url_api}productos_foraneos/filtrar_regalo/?categoria=${this.categoria_seleccionada}&page=${pagina_sumada}`)              
                this.lista_productos_encontrados = res.data.docs;
                this.pagina = res.data.page;
                this.total_paginas = res.data.totalPages;  
                //Fin de pagina siguiente con filtro
            }
        }, 
        async Metodo_detalle_producto(id_producto){
            const res = await axios.get(config.url_api + 'productos_foraneos/detalle_producto/' + id_producto)             
            this.detalle_producto = res.data; 
            
            const ids_regalos = this.regalos.map(el => el._id);
            const total_regalos = this.regalos.length;            
            const verificar_duplicado = ids_regalos.includes(this.detalle_producto._id);                       

            if(verificar_duplicado === false & total_regalos < 3 ){
                this.detalle_producto = res.data; 
                this.regalos.push(this.detalle_producto);
                localStorage.setItem('regalos-item', JSON.stringify(this.regalos));
                if(this.pagina < this.total_paginas){
                    this.Metodo_listar_sumar_productos();
                } else {

                }
            } else if(verificar_duplicado === true) {
                this.snackbar_info = true;
                this.text_snackbar = "Producto ya seleccionado"
                //this.detalle_producto = "";
            } else {
                this.snackbar_info = true;
                this.text_snackbar = "Solo puede elegir 3 regalos" 
               // this.detalle_producto = "";                
            }
             const data_analitica_favorito = {
                id_producto: this.detalle_producto._id,
                nombre_producto: this.detalle_producto.producto,
                proveedor: this.detalle_producto.proveedor,
                link_producto: this.detalle_producto.link_producto,
                precio: this.detalle_producto.precio
            };
            axios.post(config.url_api + 'analitica/agregar_producto_favorito', data_analitica_favorito);
            this.detalle_producto = "";
        }, 
        Metodo_eliminar_producto_carrito(index){
            this.regalos.splice(index, 1);
            localStorage.setItem('regalos-item', JSON.stringify(this.regalos));
            //console.log(id_producto);
        },
        Metodo_registrar_validacion(){
            const data_registrar_validacion = {
                nombre_destinatario: this.detalle_validacion.nombre_destinatario,
                whatsapp_destinatario: this.detalle_validacion.whatsapp_destinatario,
                nombre_emisor: this.detalle_validacion.nombre_emisor,
                whatsapp_emisor: this.detalle_validacion.whatsapp_emisor,
                regalos_elegidos: this.regalos
            }
                            
            if(!this.detalle_validacion.nombre_destinatario ||
                !this.detalle_validacion.whatsapp_destinatario ||
                !this.detalle_validacion.nombre_emisor ||
                !this.detalle_validacion.whatsapp_emisor
            ){
                this.error_validacion = "datos incompletos";
                this.alerta_registrar_validacion = true;
                
               //console.log("Datos incompletos") 
            } else {
                if(this.detalle_validacion.whatsapp_destinatario.length === 9 & this.detalle_validacion.whatsapp_emisor.length ===9){
                this.dialogo_registro_completo = true;
                axios.post(config.url_api + 'regalos/registrar/', data_registrar_validacion )
                    .then(res => {
                        this.regalos = [];
                        this.detalle_validacion = [];
                        this.error_validacion = "";
                        this.alerta_registrar_validacion = false;
                        localStorage.removeItem('regalos-item');
                        this.dialogo_validar_regalo = false; 
                        //console.log(res.data);            
                    });
                }else{
                    this.error_validacion = "celular 9 digitos ";
                    this.alerta_registrar_validacion = true;
                }
                
            }        

            //let RegalosDB = JSON.parse(localStorage.getItem('regalos-item'));
            //console.log(RegalosDB);
        },
        Metodo_contador_link(id_producto){
            axios.post(config.url_api + 'analitica/agregar_contador_enlace/'+ id_producto);
        }                               
              
    } 
}
</script>
