<script>
import axios from 'axios'
const config = require("../config/config");

export default {
    name: 'Demo',
    
    data() {
        return {
            dialogo_detalle_producto: false,
            dialogo_compra_realizada: false,
            dialogo_comprar_producto: false, 
            detalle_compra: [],  
            detalle_producto: [],
            alerta_registrar_compra: false,
            error_compra: [],
            principales_productos: [],
            palabra_buscar: "",
            catalogo_productos: {
                catalogo_01: "Chompas Mujer",
                subsubcategoria_catalogo_01: "chompa_mujer",
                catalogo_02: "Gorras Mujer",
                subsubcategoria_catalogo_02: "gorra_mujer",
                catalogo_03: "Ropones Bebes",
                subsubcategoria_catalogo_03: "ropones_bebes",
            },
            productos_catalogo_01: [],
            productos_catalogo_02: [],
            productos_catalogo_03: [],
            imagenes: [
                {
                    src: require("../assets/banners/background1.jpg"),
                    title: 'Encuentra de todo y para todos, pensando en tí.'
                },
            ],  
            items_celular: [
                {
                    src: require("../assets/banners_celular/banner_celular_1.jpg"),
                    title: 'Encuentra de todo y para todos, pensando en tí!'
                },                 
            ],            
            reg: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/,         
        };
    },
    created(){
         this.Metodo_listar_principales_productos();
         this.Metodo_productos_catalogo_01();
         this.Metodo_productos_catalogo_02();
         this.Metodo_productos_catalogo_03();        
    },
    mounted(){        
         window.scrollTo(0, 0);

    },
    methods: { 
      Metodo_buscar_producto(){
         this.$router.replace({
            // some route here
            path: 'productos', query: { buscar: this.palabra_buscar }
        }).catch(err => {});
      },             
        Metodo_listar_principales_productos(){
// Inicio de Metodo de listar productos
        axios.get(config.url_api + 'productos_stock/principales_productos')
        .then(res => {
            this.principales_productos = res.data;
        });
// Fin de Metodo de listar productos
        }, 
        Metodo_detalle_producto(id_producto){
            axios.get(config.url_api + 'productos_stock/detalle_producto/' + id_producto)
            .then(res => {
                this.detalle_producto = res.data;                  
            });
        },
        Metodo_comprar_producto(){
//Inicio de metodo comprar producto
        if(!this.detalle_producto.nombre_producto || 
          !this.detalle_compra.cantidad_compra ||
          !this.detalle_compra.nombre_cliente ||
          !this.detalle_compra.direccion_cliente ||
          !this.detalle_compra.correo_cliente ||
          !this.detalle_compra.celular_cliente
        ){
            this.error_compra = "datos incompletos";
            this.alerta_registrar_compra = true;          
          }else{
                if(this.detalle_compra.celular_cliente.length === 9){            
                    
                    if(!this.reg.test(this.detalle_compra.correo_cliente)){
                        this.error_compra = "correo inválido";
                        this.alerta_registrar_compra = true;
                    } else { 
                        const data_registrar_compra = {
                        nombre_producto: this.detalle_producto.nombre_producto,
                        id_producto: this.detalle_producto._id,
                        cantidad_compra: this.detalle_compra.cantidad_compra,
                        nombre_cliente: this.detalle_compra.nombre_cliente,
                        direccion_cliente: this.detalle_compra.direccion_cliente,
                        correo_cliente: this.detalle_compra.correo_cliente,
                        celular_cliente: this.detalle_compra.celular_cliente
                        } 
                            axios.post(config.url_api + 'compras/', data_registrar_compra)
                            .then(res => {
                            if(res.data.success === true){
                                this.dialogo_comprar_producto = false;
                                this.dialogo_compra_realizada = true; 
                                this.error_compra = "";
                                this.alerta_registrar_compra = false;                          
                                //Limpiar formulario
                                this.detalle_producto.nombre_producto = ""; 
                                this.detalle_producto._id = ""; 
                                this.detalle_compra.cantidad_compra = ""; 
                                this.detalle_compra.nombre_cliente = ""; 
                                this.detalle_compra.direccion_cliente = ""; 
                                this.detalle_compra.correo_cliente = ""; 
                                this.detalle_compra.celular_cliente = "";                                                  
                            } else {
                                this.error_compra = "no hay sistema";
                                this.alerta_registrar_compra = true; 
                                //Limpiar formulario
                                this.detalle_producto.nombre_producto = ""; 
                                this.detalle_producto._id = "";
                                this.detalle_compra.cantidad_compra = ""; 
                                this.detalle_compra.nombre_cliente = ""; 
                                this.detalle_compra.direccion_cliente = ""; 
                                this.detalle_compra.correo_cliente = ""; 
                                this.detalle_compra.celular_cliente = "";                           
                            }
                            });                        
                    }
                                                        
                } else {
                        this.error_compra = "celular incorrecto";
                        this.alerta_registrar_compra = true;
                }
          }
//Fin de metodo comprar producto
        },
        Metodo_productos_catalogo_01(){
        //Inicio de listar productos catalogo 01        
        return axios.get(`${config.url_api}productos_stock/catalogo_productos/?query=${this.catalogo_productos.subsubcategoria_catalogo_01}`)
          .then(res => {
              this.productos_catalogo_01 = res.data;
          });  
        //Fin de listar productos catalogo 01        
        },
        Metodo_productos_catalogo_02(){
        //Inicio de listar productos catalogo 02        
        return axios.get(`${config.url_api}productos_stock/catalogo_productos/?query=${this.catalogo_productos.subsubcategoria_catalogo_02}`)
          .then(res => {
              this.productos_catalogo_02 = res.data;
          });  
        //Fin de listar productos catalogo 02        
        },
        Metodo_productos_catalogo_03(){
        //Inicio de listar productos catalogo 03       
        return axios.get(`${config.url_api}productos_stock/catalogo_productos/?query=${this.catalogo_productos.subsubcategoria_catalogo_03}`)
          .then(res => {
              this.productos_catalogo_03 = res.data;
          });  
        //Fin de listar productos catalogo 03        
        },                               
    } 
}
</script>
