<script>
import axios from 'axios'
const configuraciones = require("@/config/config");


export default {
    data() {
        return {
            icons: [
                'mdi-facebook',
                'mdi-twitter',
                'mdi-linkedin',
                'mdi-instagram',
            ],
            data_registrar_mensaje: [{
                nombre_completo: "",
                celular: "",
                correo: "",
                mensaje: ""
            }],
            alerta_error_registrar_mensaje: false,
            erros: [],
            snackbar_registro_exitoso: false,
            snackbar_error: false,
            snackbar_info: false,
            timeout: 8000,
            text_snackbar: [],
            reg: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/,      
                   
        }
    },
     mounted(){
        window.scrollTo(0, 0)
    },
    methods: {
        Metodo_registrar_mensaje(){
// Inicio de Metodo de Cotizar Servicio          
            if(!this.data_registrar_mensaje.nombre_completo ||
                !this.data_registrar_mensaje.celular ||
                !this.data_registrar_mensaje.correo ||
                !this.data_registrar_mensaje.mensaje
                ){
                    this.alerta_error_registrar_mensaje = true;
                    this.erros = "datos incompletos"
                }else {
                    if(this.data_registrar_mensaje.celular.length === 9){

                    if(!this.reg.test(this.data_registrar_mensaje.correo)){                        
                        this.erros = "correo inválido";
                        this.alerta_error_registrar_mensaje = true;
                    } else {                    
                    const data_enviar_servidor = {
                        nombre_completo: this.data_registrar_mensaje.nombre_completo,
                        celular: this.data_registrar_mensaje.celular,
                        correo: this.data_registrar_mensaje.correo,
                        mensaje: this.data_registrar_mensaje.mensaje
                    }
                    axios.post(configuraciones.url_api + 'mensajes/registrar_mensaje/', data_enviar_servidor )
                    .then(res => {
                        if(res.data.success === true){
                            this.text_snackbar = "Gracias por escribirnos";
                            this.snackbar_registro_exitoso = true;
                            this.dialogo_cotizar_servicio = false;
                            //Limpiar formulario
                            this.data_registrar_mensaje.nombre_completo = "";
                            this.data_registrar_mensaje.celular = "";
                            this.data_registrar_mensaje.correo = "";
                            this.data_registrar_mensaje.mensaje = "";
                            this.alerta_error_registrar_mensaje = false;
                            this.erros = ""                            
                        }else{
                            this.text_snackbar = res.data.msg;
                            this.snackbar_error = true;
                            this.dialogo_cotizar_servicio = false;
                            //Limpiar formulario
                            this.data_registrar_mensaje.nombre_completo = "";
                            this.data_registrar_mensaje.celular = "";
                            this.data_registrar_mensaje.correo = "";
                            this.data_registrar_mensaje.mensaje = "";
                            this.alerta_error_registrar_mensaje = false;
                            this.erros = ""                               
                        }
                    });                       
                    }

                    }else {
                        this.erros = "celular incorrecto";
                        this.alerta_error_registrar_mensaje = true;
                    }
                }      
// Fin de Metodo de Cotizar Servicio                                     
        }          
    }
}
</script>
